.main{
  padding: 1.875em 0 3.125em;
  overflow: hidden;
  &__top{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 17.625em;
  }
  &__logo{
    width: 21.875em;
    margin-right: 10.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1.25em;
  }
  &__desc{
    max-width: 15.625em;
    margin-right: 11.4375em;
  }
}

.main-img{
  margin-top: 3.125em;
  img{
    display: block;
    width: 100%;
  }
}

.catalog{
  padding: 3.125em 0;
  &__title{
    text-align: center;
  }
  &__items{
    margin-top: 3.125em;
    @include grid100gap(1.25em);
  }
}

.catalog-card{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .3125em));
  gap: .625em;
  background-color: $gray;
  align-items: flex-end;
  &__slider{
    position: relative;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-nav{
      position: absolute;
      right: 3.125em;
      bottom: 3.125em;
      z-index: 2;
    }
  }
  &__img{
    height: 31.25em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 3.125em;
  }
  &__info{
    max-width: 37.5em;
    @include grid100gap(.625em);
  }
  &__desc{
    max-width: 23em;
  }
  @media screen and (min-width: 993px){
    &:nth-child(even){
      .catalog-card__content{
        order: -1;
      }
    }
    ._grid2,
    ._grid3{
      .swiper{
        &-wrapper{
          transform: unset !important;
          display: grid;
          gap: .625em;
        }
        &-slide {
          width: 100% !important;
        }
        &-nav{
          display: none;
        }
      }
    }
    ._grid2{
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .3125em));
        }
        &-slide{
          &:nth-child(2){
            .catalog-card__img{
              &:after{
                left: 0;
                right: unset;
                border-left: unset;
                border-right: 9.375em solid transparent;
              }
            }
            &~.swiper-slide{
              display: none;
            }
          }
        }
      }
    }
    ._grid3{
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(2,calc(50% - .3125em));
          height: 31.25em;
        }
        &-slide{
          .catalog-card__img{
            height: 100%;
          }
          &:nth-child(1){
            grid-row: 1/3;
          }
          &:nth-child(2),
          &:nth-child(3){
            .catalog-card__img{
              &:after{
                display: none;
              }
            }
          }
          &:nth-child(3){
            .catalog-card__img{
              &:after{
                //left: 0;
                //right: unset;
                //border-left: unset;
                //border-right: 9.375em solid transparent;
              }
            }
            &~.swiper-slide{
              display: none;
            }
          }
        }
      }
    }
    ._no-right-triangle{
      .swiper-slide:nth-child(1){
        .catalog-card__img:after{
          left: 0;
          right: unset;
          border-left: unset;
          border-right: 9.375em solid transparent;
        }
      }
      .swiper-slide:nth-child(2){
        .catalog-card__img:after{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1em 0;
    &__top{
      flex-direction: column;
      justify-content: unset;
      padding: 0;
    }
    &__logo{
      display: none;
    }
    &__btn{
      width: 100%;
    }
    &__info{
      flex-direction: column;
      text-align: center;
    }
    &__desc{
      order: 2;
      margin: 1em 0 0;
    }
  }

  .main-img{
    margin-top: 1.25em;
    img{
      height: 16em;
      width: auto;
      margin-left: -2em;
    }
  }

  .catalog{
    padding: 1em 0;
    &__title{
      text-align: left;
    }
    &__items{
      margin-top: 2em;
    }
  }

  .catalog-card{
    grid-template-columns: 100%;
    gap: 0;
    overflow: hidden;
    &__slider{
      padding-right: .5em;
    }
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        padding-right: .5em;
      }
      &-nav{
        display: none;
      }
    }
    &__img{
      padding-bottom: 75%;
      height: unset;
      overflow: hidden;
      &:after{
        display: none;
      }
      img{
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    &__content{
      padding: 1.25em;
    }
    &__info{
      gap: .5em;
    }
  }
}
