@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-size: 1em;
    leading-trim: both;
    text-edge: cap;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-double{
    padding: 0 5em;
    height: 5.6875em;
    border-radius: 31.25em;
    background-color: $primary;
    color: #fff;
    margin-right: 5.6875em;
    position: relative;
    &:after{
        content: '';
        position: absolute;
        left: 100%;
        top: 0;
        display: block;
        width: 5.6875em;
        height: 5.6875em;
        background-color: inherit;
        margin-left: -.125em;
        border-radius: 50%;
        background-image: url(../img/arrow-right.svg);
        @include bgImgSize(1.5em);
    }
    &:hover{
        background-color: $primaryHover;
        color: #fff;
        &:after{
            background-color: inherit;
        }
    }
    &:active{
        background-color: $primaryActive;
        color: #fff;
        &:after{
            background-color: inherit;
        }
    }
}

.swiper-nav{
    display: flex;
    .swiper-button{
        position: static;
        margin: 0;
    }
}

.swiper-button{
    border: 2px solid #FFFFFF;
    border-radius: 31.25em;
    width: 4.375em;
    height: 2.5em;
    @include center;
    img{
        width: 1.25em;
        display: block;
    }
}


@media screen and (max-width: 992px){
    .m-btn-double{
        margin-right: 0;
        padding: 0 1.5em;
        height: 3.5em;
        &:after{
            display: none;
        }
        span{
            font-size: .875em;
        }
    }
}
