.header{
  padding: 1.25em 0;
  font-family: $font2;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 7.75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    flex-shrink: 0;
    padding: 0 1.875em;
    border: 2px solid $primary;
    border-radius: 500px;
    height: 3.1875em;
    &:hover{
      background-color: $primary;
      color: #fff;
    }
  }
}

.menu{
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 4em;
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    text-transform: uppercase;
    &:not(:last-child){
      margin-right: 3.125em;
    }
    span{
      font-size: 1.125em;
      line-height: 130%;
      letter-spacing: 0.05em;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.5em;
    flex-shrink: 0;
    @include lastMr(1.25em);
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-30%);
      }
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__link{
    text-transform: uppercase;
    @include lastMr(1.875em);
    span{
      font-size: 1.125em;
      line-height: 139%;
    }
  }
}

.burger{
  display: none;
}



@media screen and (max-width: 992px){
  .header{
    padding: .75em 0;
    &__logo{
      width: 6.5em;
      position: relative;
      z-index: 11;
    }
    &__btn{
      margin-top: 2em;
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
      }
      .menu{
        transform: translateX(0%);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    margin-left: auto;
    display: flex;
    flex-shrink: 0;
    width: 1.75em;
    height: 1.75em;
    position: relative;
    z-index: 11;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
  }

  .menu {
    position: fixed;
    bottom: 0;
    left: 0;
    overflow-y: auto;
    height: $fullHeight;
    width: 100%;
    background: #fff;
    z-index: 10;
    padding: 6em 1.25em 2em;
    margin: 0;
    flex-direction: column;
    justify-content: unset;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-200%);
    transition: all .4s;
  }

  .nav,
  .header-contacts{
    flex-direction: column;
    &__link{
      @include lastMr(0);
      @include lastMb(1em);
    }
  }

  .socials{
    margin-top: 2em;
  }

  .header-contacts{
    margin-top: 2em;
  }
}
