.text80,
.text46,
.text40{
  text-transform: uppercase;
  font-family: $font2;
}

.text80{
  font-size: 5em;
  line-height: 140%;
}

.text46{
  font-size: 2.875em;
  line-height: 110%;
}

.text40{
  font-size: 2.5em;
  line-height: 140%;
  letter-spacing: 0.05em;
}

.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text22{
  font-size: 1.375em;
  line-height: 150%;
}

.primary-text{
  color: $primary;
}

.dark-text{
  color: $dark;
}


@media screen and (max-width: 992px){
  .text80,
  .text46{
    font-size: 1.75em;
  }

  .text40,
  .text36{
    font-size: 1.5em;
  }

  .text24{
    font-size: 1.125em;
  }

  .text22{
    font-size: .875em;
  }
}
