@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._triangle{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-top: 9.375em solid $gray;
    border-left: 9.375em solid transparent;
    top: 0;
    right: 0;
  }
}
